.todo-item {
  display: flex;
  padding: 1rem;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
  color: #939597;
  background-color: #f5df4d;
  margin: 0.5rem 0;
  border-radius: 3px;
  font-weight: 500;
  font-size: 1.5rem;
}

@media (hover: hover) {
  .todo-item:hover {
    background-color: #939597;
    color: #f5df4d;
  }
}

.todo-item:hover .remove {
  opacity: 1;
}

.remove {
  opacity: 0;
  margin-right: 1rem;
  color: #a82828;
  font-weight: 600;
}

.priority {
  width: 3.5rem;
}

.todo-text {
  flex: 1;
  word-break: break-all;
  user-select: text;
}

.checked {
  text-decoration: line-through;
  color: #adb5bd;
}
